.contenedor-testimonio{
    width: 1170px;
    height: 400px;
    margin: 10px 10px 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #ffffff;
    box-shadow: 0 3px 13px 1px rgb(0, 0, 0 / 9%);
}
.imagen-testimonio{
    height: 100%;
    width: auto;
}
.contenedor-texto-testimonio{
    width: 100%;
    font-size: 18px;
    text-align: center;
    padding: 40px;
    font-family: Lato, sans-serif;
    line-height: 1.5rem;
    font-weight: 400;
}
.nombre-testimonio{
    font-size: 1.3rem;
}
.cargo-testimonio{
    font-size: 1.3rem;
    margin-top: 15px;
    padding-bottom: 30px;
}
.text-testimonio{
    font-size: 1.1rem;
    text-align: center;
}